<template>
  <div class="content">

    <countdown :time="countdown" @countdownend="countdownend" class="task-countdown">
      <template slot-scope="props"><van-icon name="hint"/>{{ props.hours || '00' }}{{ +props.minutes || '00' }}{{ +props.seconds || '00' }}</template>
    </countdown>

    <div class="pay_way_group" style="margin-top: 30px">
      <div class="pay_way_title"></div>
      <van-cell-group class="payment_group">
        <van-cell title="">{{salesTask.goods.shop_name}}</van-cell>
        <van-cell style="color: red;">
          <template slot="title">
            <span class="van-cell-text"></span>
            <van-icon slot="right-icon" name="question" class="van-cell__right-icon" @click="showHelp(1)"/>
          </template>
          <template onselectstart="return false;" class="no-longtap" ontouchstart="return false;">
            <span class="van-cell-text no-longtap"  onselectstart="return false;" ontouchstart="return false;">{{salesTask.goods.key}}</span>
          </template>
        </van-cell>
        <van-cell title="">
          {{salesTask.goods.goods_price}}
        </van-cell>
        <van-cell title="">
          <template slot="title">
            <span class="van-cell-text"></span>
            <van-icon slot="right-icon" name="question" class="van-cell__right-icon" @click="showHelp(2)"/>
          </template>
          {{salesTask.goods.num}}
        </van-cell>
        <van-cell title="">
          <template slot="title">
            <span class="van-cell-text"></span>
            <van-icon slot="right-icon" name="question" class="van-cell__right-icon" @click="showHelp(5)"/>
          </template>
          {{salesTask.goods.goods_price * salesTask.goods.num | money}}
        </van-cell>
        <!--<van-cell title="">{{salesTask.revisePrice}}</van-cell>-->
        <!--<div class="van-cell"><span></span></div>-->
        <van-cell title="">
          <template slot="title">
            <span class="van-cell-text"></span>
            <van-icon slot="right-icon" name="question" class="van-cell__right-icon" @click="showHelp(3)"/>
          </template>
        </van-cell>
      </van-cell-group>
      <img v-lazy="salesTask.goods.main_img" width="100%" @click="showImagePreview(salesTask.goods.main_img)">
    </div>

    <div class="pay_way_group">
      <div class="pay_way_title"></div>
      <van-cell-group class="payment_group">
        <!--<van-cell title="">{{salesTask.searchKey}}</van-cell>-->
        <!--<van-cell title="">{{salesTask.sortord}}</van-cell>-->
        <van-cell title="">{{salesTask.task.address}}</van-cell>
        <van-cell title="">{{salesTask.task.range}}</van-cell>
        <van-cell title="">{{salesTask.task.browsing_time}}</van-cell>
        <van-cell title="">{{salesTask.task.conllect}}</van-cell>
        <van-cell title="">{{salesTask.task.shopping_cart}}</van-cell>
        <van-cell title="">{{salesTask.task.chat == '' ? salesTask.task.chat_remark : ''}}</van-cell>
        <van-cell title="">{{salesTask.task.remark || ''}}</van-cell>
      </van-cell-group>
    </div>

    <div class="pay_way_group">
      <div class="pay_way_title"></div>
      <van-cell-group class="payment_group">
        <van-field v-model="shopName" type="text" placeholder="" :error="errorInfo.shopName">
          <van-button slot="button" size="small" type="primary" @click="validShopName()"></van-button>
        </van-field>
      </van-cell-group>
    </div>

    <div class="pay_way_group">
      <div class="pay_way_title">1<span style="color: darkred; margin-left: 5px;"></span><van-icon @click="showHelp(4)" slot="right-icon" name="question" class="van-cell__right-icon" /></div>
      <p class="text_style">
        2-32-31
      </p>
      <p class="text_red">
        
      </p>
      <van-cell-group>
        <van-field v-model="others1" type="textarea"  placeholder="" :error="errorInfo.others1"></van-field>
        <!-- <van-field v-model="others2"  placeholder="2" :error="errorInfo.others2"></van-field>
        <van-field v-model="others3"  placeholder="3"  :error="errorInfo.others3"></van-field> -->
      </van-cell-group>
    </div>

    <div class="pay_way_group">
      <div class="pay_way_title"></div>
      <p class="text_style">
        2-31-2()<br>1
      </p>
      <p class="text_red">
        
      </p>
      <van-cell-group class="payment_group">
        <van-field v-model="theShop" type="textarea" placeholder="" :error="errorInfo.theShop"></van-field>
      </van-cell-group>
    </div>

    <div v-if="salesTask.pick_details.length > 0" class="pay_way_group" style="padding-bottom: 10px;">
      <div class="pay_way_title"></div>
      <van-row class="upload_hint text-desc">
        <van-icon name="hint"/>
        : /
      </van-row>
      <van-row class="vacancy_row" v-for="(pick, i) in salesTask.pick_details" :key="i">
        <van-col span="8">{{pick.front}}</van-col>
        <van-col span="8">
          <input :ref="'inputDetail' + i" type="text" placeholder="" class="van-field__control"  :pid="pick.id">
        </van-col>
        <van-col span="8">{{pick.ending}}</van-col>
      </van-row>
    </div>

    <div v-if="salesTask.pick_comments.length > 0" class="pay_way_group" style=" padding-bottom: 10px;">
      <div class="pay_way_title"></div>
      <van-row class="upload_hint text-desc">
        <van-icon name="hint"/>
        : 201806012530
      </van-row>
      <van-row class="vacancy_row" v-for="(pick, i) in salesTask.pick_comments" :key="i">
        <van-col span="8">{{pick.comments}}</van-col>
        <van-col span="16"><input :ref="'inputComment' + i" type="text" class="van-field__control"  placeholder="" :pid="pick.id"></van-col>
      </van-row>
    </div>

    <div class="pay_way_group">
      <div class="pay_way_title">:<span style="color: darkred;">100</span></div>
      <p v-if="orderType == 1" class="text_style">
        1
      </p>

      <p v-if="orderType == 2" class="text_style">
        <br>
      </p>
      <van-cell-group class="payment_group">
          <van-field v-model="orderNo" type="text"  placeholder="" :error="errorInfo.orderNo" />
      </van-cell-group>
    </div>

    <div class="pay_way_group">
      <div class="pay_way_title">:<span style="color: darkred;"></span></div>
      <van-cell-group class="payment_group">
          <van-field v-model="tbMoney" type="text"  placeholder=""/>
      </van-cell-group>
    </div>

    <countdown v-if="counting" slot="button" :time="180000" @countdownend="countdownend" class="countdown van-button">
      <template slot-scope="props">{{ +props.minutes || '00' }}{{ +props.seconds || '00' }}</template>
    </countdown>

    <van-button v-else class="pay_submit" @click="submit" :loading="isSubmit"
                type="danger" :bottom-action="true">
    </van-button>

    <van-button class="cannel_submit" @click="cancel" type="default" :bottom-action="true">
    </van-button>
  </div>
</template>

<script>
  import {
    Row,
    Col,
    CellGroup,
    Cell,
    Button,
    Dialog,
    ImagePreview,
    Toast,
    NavBar,
    Field
  } from "vant";
  import { SALES, CANCEL, VALID_SHOP} from "@/api/task";
  import img01 from '@/assets/images/img01.png'
  import img02 from '@/assets/images/img02.png'
  import img03 from '@/assets/images/img03.png'
  import img04 from '@/assets/images/img04.png'
  import img05 from '@/assets/images/img05.png'
  import img06 from '@/assets/images/img06.png'
  import img07 from '@/assets/images/img07.png'

  export default {
    name: "salesDetails",
    data() {
      return {
        isSubmit : false,
        countdown: 210000,
        shopName :'',
        others1 : '',
        theShop:'',
        tbMoney:'',
        orderNo: '',
        orderType: '',
        counting: true,
        salesTask: {
          "task": {
            "id": "",
            "type": "",
            "overtime": "",
            "conllect": "",
            "shopping_cart": "",
            "chat": "",
            "screenshot_pay": "",
            "screenshot_search": "",
            "address": "",
            "range": "",
            "browsing_time": "",
            "chat_remark":"",
            "remark": "",
            "status": "",
            "othersExample": "",
          },
          "goods": {
            "id": "",
            "key": "",
            "main_img": "",
            "price": "",
            "shop_name": ""
          },
          "pick_details": [
            {
              "id": "",
              "front": "",
              "ending": "",
              "answer": ""
            }
          ],
          "pick_comments": [
            {
              "id": "",
              "comments": "",
              "answer": ""
            }
          ]
        },
        errorInfo: {
          shopName: false,
          others1:false,
          theShop: false,
          orderNo: false
        }
      };
    },
    props: {
      id: this.id
    },
    watch:{
			'$route': 'changeRouter'
		},
    created(){
      this.initPage();
    },

    methods: {
      copytouch(e,s) {
        e.preventDefault=true
        alert('')
      },
      initPage(){
        if(!this.id){
          return;
        }
        this.$reqGet(SALES + this.id).then((res) => {
          this.getSuccess(res);
        }).catch((err) => {
          this.getSuccess(err);
        });
      },
      getSuccess(res){
        let result = res.data;
        if(!result || result.code != 0){
          let msg = result.msg ? result.msg : "";
          Toast.fail(msg);
          return;
        }
        let data = result.data;
        this.salesTask = data;
        this.orderType = data.task.order_type;
        this.countdown = this.salesTask.task.overtime;
      },
      validShopName(){
        if(!this.shopName){
          Toast.fail('');
          return;
        }
        this.$reqPost(VALID_SHOP, {
          order_id:this.id,
          shop_name:this.shopName
        }).then((res) => {
          Toast(res.data.msg);
        }).catch((err) => {
          Toast(err.data.msg);
        });
      },
      submit() {
        let details = this.salesTask.pick_details;
        let check = true;
        let items = [
          'shopName',
          'others1',
          'theShop',
          'orderNo'
        ];
        check = items.every(item => {
          let msg = this.getErrorMessage(item);
          if (msg) {
            this.errorInfo[item] = true;
            Toast.fail(msg);
          }
          return !msg;
        });
        if(!check){
          Toast.fail('');
        }
        let formdata = new FormData();
        formdata.append("order_id", this.id);
        formdata.append("tb_order_id", this.orderNo);
        formdata.append("others1", this.others1);
        formdata.append("shopName", this.shopName);
        formdata.append("theShop", this.theShop);
        formdata.append("tbMoney", this.tbMoney);
        this.$reqPost(SALES, formdata).then((res) => {
          this.submitSuccess(res);
        }).catch((err) => {

        });
      },
      getErrorMessage(key) {
        switch (key) {
          case "shopName":
            return this.shopName ? "" : "";
          case "others1":
            return this.others1 ? "" : "3";
          case "theShop":
            return this.theShop ? "" : "";
          case "orderNo":
            return this.orderNo ? "" : "";
        }
      },
      submitSuccess(res) {
        let data = res.data;
        if(this.orderType == 1){
            var tipStr=`<p style="text-align: center;">1</p>
            <p style="color: darkred; text-align: center;">1%</p>`
        }
        if(this.orderType == 2){
            var tipStr=`<p style="text-align: center;"></p>
            <p style="color: darkred; text-align: center;">~</p>
            <p style="color:red;text-align:center">(1%)</p>
            `
        }
        Dialog.alert({
          message:tipStr
        }).then(() => {
           this.$router.push({ path: "/orders"})
        }).catch((err) => {
          // console.log(err);
        });
      },

      showImagePreview(img){
        ImagePreview([img]);
      },
      countdownend() {
        this.counting = false;
      },

      cancel(){
        Dialog.confirm({
          title: '',
          message: ''
        }).then(() => {
          // on confirm
          this.$reqPost(CANCEL, {
            order_id:this.id
          }).then((res) => {
            Toast(res.data.msg);
            if(res.data.code == 0){
              this.$router.go(-1);
            }
          }).catch((err) => {
            Toast(err.data.msg);
            if(err.data.code == 0){
              this.$router.go(-1);
            }
          });
        }).catch(() => {
          // on cancel
        });
      },

      showHelp(type){
        switch (type){
          case 1:
            Toast({
              message: '',
              position: 'top'
            });
            break;
          case 2:
            Toast({
              message: '',
              position: 'top'
            });
            break;
          case 3:
            Toast({
              message: '',
              position: 'top'
            });
            break;
          case 4:
            // this.showImagePreview(this.salesTask.task.othersExample);
            ImagePreview([
              img01,
              img02,
              img03,
              img04,
              img05,
              img06,
              img07
            ]);
            break;
          case 5:
            Toast({
              message: '=*100',
              position: 'top'
            });
            break;
        }
      }
    },

    components: {
      [Row.name]: Row,
      [Col.name]: Col,
      [CellGroup.name]: CellGroup,
      [Cell.name]: Cell,
      [Button.name]: Button,
      [ImagePreview.name]: ImagePreview,
      [Toast.name]: Toast,
      [NavBar.name]:NavBar,
      [Field.name]:Field
    },

    filters: {
      money: function(val) {
        val = val.toString().replace(/\$|\,/g,'');
        if(isNaN(val)) {
          val = "0";
        }
        let sign = (val == (val = Math.abs(val)));
        val = Math.floor(val*100+0.50000000001);
        let cents = val%100;
        val = Math.floor(val/100).toString();
        if(cents<10) {
          cents = "0" + cents
        }
        for (var i = 0; i < Math.floor((val.length-(1+i))/3); i++) {
          val = val.substring(0,val.length-(4*i+3))+',' + val.substring(val.length-(4*i+3));
        }
        return (((sign)?'':'-') + val + '.' + cents);
      }
    }
  };

</script>

<style lang="scss" scoped>

  // .payment_group {
  //   /*padding: 10px 15px;*/
  // }

  .task-countdown {
    text-align: center;
    font-size: 12px;
    background-color: #fffeec;
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    line-height: 30px;
    z-index: 999;
  }

  .pay_submit {
    /*position: fixed;*/
    /*bottom: 0;*/
    width: 100%;
  }

  .cannel_submit{
    margin-top:10px;
    width: 100%;
  }

  .pay_way_group{
    background-color: #ffffff;
    margin-bottom: 10px;
  }

  .pay_way_group img {
    vertical-align: middle;
  }

  .pay_way_title {
    font-weight: bold;
    padding: 15px;
    background-color: #fff;
  }

  .upload_hint{
    position: relative;
    padding-left: 20px;
    line-height: 1.6;
    margin: 10px;
    i{
      position: absolute;
      top: 2px;
      left: 0;
    }
  }

  .vacancy_row{
    .van-col {
      font-size: 13px;
      line-height: 30px;
      text-align: center;
      vertical-align: middle;
      margin-bottom: 0px;
      background-clip: content-box;
    }
  }

  .countdown{
    text-align: center;
    width: 100%;
    height: 50px;
    line-height: 50px;
    border: 0;
    border-radius: 0;
    font-size: 16px;
    color: #fff;
    background-color: #29a0f6;
  }

  .van-cell__right-icon {
    top: 3px;
  }

  .text_style {
    color: #29a0f6;
    font-size: 12px;
    padding-left: 15px;
    text-align: left;
    margin: 0;
    padding-bottom: 7px;
  }
  .text_red{
    color:red;
    font-size: 12px;
    padding-left: 15px;
    text-align: left;
    margin: 0;
    padding-bottom: 7px;
  }
  .no-longtap{
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
  }
</style>



